}
.one{
  flex: 2 1 auto;
}

.toto{
  position: fixed;
  top:50%;
  left: 50%;
  transform:rotate(180deg);
  z-index: 9999;
  transform: scale(4,4);
  opacity: 1;
  box-shadow: 10px 10px 10px rgb(27, 18, 18);
  background-color: yellow;
  animation: 1s linear 0s moveeye;
}

@keyframes moveeye{
  0%{
    box-shadow: 2px 2px 2px rgb(27, 18, 18);
    transform: scale(1,1);
    transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    box-shadow: 4px 4px 4px rgb(27, 18, 18);
    transform: scale(3,3);
    opacity: 0.4;
    transform:rotate(90deg);
  }
}

.but{
  background-color: red;
}


