.toto{
  position: fixed;
  top:50%;
  left: 50%;
  -webkit-transform:rotate(180deg);
          transform:rotate(180deg);
  z-index: 9999;
  -webkit-transform: scale(4,4);
          transform: scale(4,4);
  opacity: 1;
  box-shadow: 10px 10px 10px rgb(27, 18, 18);
  background-color: yellow;
  -webkit-animation: 1s linear 0s moveeye;
          animation: 1s linear 0s moveeye;
}

@-webkit-keyframes moveeye{
  0%{
    box-shadow: 2px 2px 2px rgb(27, 18, 18);
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    box-shadow: 4px 4px 4px rgb(27, 18, 18);
    -webkit-transform: scale(3,3);
            transform: scale(3,3);
    opacity: 0.4;
    -webkit-transform:rotate(90deg);
            transform:rotate(90deg);
  }
}

@keyframes moveeye{
  0%{
    box-shadow: 2px 2px 2px rgb(27, 18, 18);
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    box-shadow: 4px 4px 4px rgb(27, 18, 18);
    -webkit-transform: scale(3,3);
            transform: scale(3,3);
    opacity: 0.4;
    -webkit-transform:rotate(90deg);
            transform:rotate(90deg);
  }
}

